<template>
	<view>
		<view>
			<u-navbar 
			:is-back="true" 
			back-text="返回" 
			back-icon-color="white" 
			:back-text-style="{ color: 'white' }" 
			title="新用户注册" 
			:background="bgColor" 
			title-color="white" 
			title-size="40">
			</u-navbar>
			<u-top-tips ref="uTips" :navbar-height="statusBarHeight + navbarHeight"></u-top-tips>
		</view>
		
		<view class="regist">
			<view class="title">
				注<span>册</span>
			</view>
			
			<view class="input">
				<view id="username">用户名</view>
				<view class="inputUsername">
					<u-input v-model="registForm.username" type="text" border="true" :border-color="username_borderColor" placeholder="请输入用户名" @blur="checkUsername()" />
				</view>
			</view>
			
			<view class="input">
				<view id="password">密码</view>
				<view class="inputPassword">
					<u-input v-model="registForm.password" type="password" border="true" password-icon="true" :border-color="password_borderColor" placeholder="请输入密码" /> 
				</view>
			</view>
			
			<view class="input">
				<view id="againPassword">确认密码</view>
				<view class="inputAgainPassword">
					<u-input v-model="registForm.passwordAgain" type="password" border="true" password-icon="true" :border-color="password_borderColor" @blur="checkpassword()" placeholder="请再次输入密码" /> 
				</view>
			</view>
			
			<view class="input">
				<view id="checkCode">验证码</view>
				<view class="inputCheckCode">
					<u-input v-model="registForm.checkCode" type="text" border="true" :border-color="checkCode_borderColor" placeholder="请输入验证码" /> 
				</view>
				<img class="checkCodeImg" :src="checkCodeSrc" @click="getImg()">
			</view>
			
			
			<view class="submitButton" >
				<u-button type="primary" :disabled="buttonDisabled" @click="regist()">注册</u-button> 
			</view>
			
			
			
			
		</view>	
	</view>
</template>

<script>
	export default{
		data(){
			return{
				username_borderColor: '#dcdfe6',
				password_borderColor: '#dcdfe6',
				checkCode_borderColor: '#dcdfe6',
				bgColor:{
				  "backgroundImage": "linear-gradient(45deg, #1E90FF, 	#BA55D3)"
				},
				// 状态栏高度，H5中，此值为0，因为H5不可操作状态栏
				statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
				// 导航栏内容区域高度，不包括状态栏高度在内
				navbarHeight: 44,
				registForm:{
					username:'',
					password:'',
					passwordAgain:'',
					checkCode:''
				},
				checkCodeSrc:'http://localhost/checkCodes?'+ new Date().getTime(),
				buttonDisabled:false
			}
		},
		
		
		methods:{
			getImg(){
				this.checkCodeSrc = 'http://localhost/checkCodes?'+ new Date().getTime();
			},
			checkUsername(){
				console.log(this.registForm.username);
				uni.request({
					url:'http://localhost/users/checkUsername',
					method:"POST",
					data:{
						username: this.registForm.username
					},
					success: (e) => {
						console.log(e);
						if(! e.data.flag){
							this.$refs.uTips.show({
								title: e.data.msg,
								type: 'error'
							});
							this.username_borderColor = 'red';
							this.buttonDisabled = true;	
							this.registForm.username = null;
						}else{
							this.username_borderColor = '#dcdfe6';
							this.buttonDisabled = false;	
						}
					}
				})
			},
			checkpassword(){
				console.log(this.registForm.password);
				console.log(this.registForm.passwordAgain);
				if(this.registForm.password != this.registForm.passwordAgain){
					this.$refs.uTips.show({
						title: '两次密码不一致',
						type: 'error'
					});
					this.password_borderColor = 'red';
					this.buttonDisabled = true;		
					this.registForm.passwordAgain = null;
				}else{
					this.password_borderColor = '#dcdfe6';
					this.buttonDisabled = false;
				}
			},
			regist(){
				console.log(this.registForm.checkCode)
				uni.request({
					url:'http://localhost/checkCodes',
					method: "POST",
					success: (e) => {
						console.log(e);
						if(this.registForm.checkCode != e.data.data.checkCode){
							this.$refs.uTips.show({
								title: '验证码错误',
								type: 'error'
							});
							
							this.checkCode_borderColor = 'red';
							this.registForm.checkCode = null;
							this.getImg();
						}else{
							this.checkCode_borderColor = '#dcdfe6';
							uni.request({
								url: 'http://localhost/users/register',
								method: "POST",
								data: {
									username: this.registForm.username,
									password: this.registForm.password
								},
								success: (e) => {
									console.log(e); 
									uni.showToast({
										icon:"success",
										title:'注册成功'
									});
									setTimeout(function(){
										uni.navigateBack({
											
										});
									},2000);
								}
							})
						}
					}
				})
				
			}
			
		}
	}
</script>

<style scoped>
	.regist{
		width: 700rpx;
		margin-left: 25rpx;
		/* background-color: white; */
		margin-top: 80rpx;
	}
	.title{
		font-size: 90rpx;
		color: #1E90FF;
		width: 500rpx;
		padding-left: 20rpx;
		/* margin-left: 50rpx; */
		border-bottom: 1px solid #1E90FF;
	}
	.title span{
		margin-left: 25rpx;
		font-size: 38rpx;
		color: #BA55D3;
	}
	.input{
		display: flex;
		margin-top: 30rpx;
	}
	.inputPassword{
		width: 500rpx;
		/* border: 1px solid #000; */
		margin-left: 20rpx;
	}
	.inputAgainPassword{
		width: 500rpx;
		margin-left: 50rpx;
	}
	.inputUsername{
		width: 500rpx;
		margin-left: 16rpx;
	}
	.inputCheckCode{
		width: 250rpx;
		margin-left: 16rpx;
	}
	#username,#password{
		font-size: 30rpx;
		height: 70rpx;
		line-height: 70rpx;
		margin-right: 30rpx;
/* 		border: 1px solid #000; */
		margin-left: 35rpx;
	}
	#password{
		margin-left: 60rpx;
	}
	#againPassword{
		font-size: 30rpx;
		height: 70rpx;
		line-height: 70rpx;
	}
	#checkCode{
		font-size: 30rpx;
		height: 70rpx;
		line-height: 70rpx;
		padding-left: 30rpx;
		margin-right: 35rpx;
	}
	.checkCodeImg{
		width: 230rpx;
		height: 70rpx;
		margin-left: 20rpx;
	}
	.submitButton{
		margin-top: 50rpx;
		width: 500rpx;
		margin-left: 100rpx;
	}
</style>